:root {
    --clr-dark: #181E25;
    --clr-primary: #a918a0;
    --clr-secondary: #5F7086;
    --clr-gray-light: #DEE4ED;
    --clr-pink: rgba(207, 93, 113, 1);
    --clr-yellow: rgba(255, 208, 129, 1);
    --clr-gray: #F3F5F7;
    --clr-white: #FFFFFF;
    --clr-blue: #5942E9;



    --ff-primary: 'General Sans';
}
*,
*::before,
*::after {
    box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h5,
p {
    margin: 0;
}

html {
    scroll-behavior: smooth;
    font-size: 100%;
}

body {
    min-height: 100vh;
    font-family: var(--ff-primary);
}

img,
video,
iframe {
    max-inline-size: 100%;
    block-size: auto;
}

input,
button,
textarea,
select,
a {
    font: inherit;
}

a {
    text-decoration: none;
    color: inherit;
}

/* Remove all animations, for users who have turned them off */
@media (prefers-reduced-motion: reduce) {
    html:focus-within {
        scroll-behavior: auto;
    }

    *,
    *::before,
    *::after {
        -webkit-animation-duration: 0.01ms !important;
                animation-duration: 0.01ms !important;
        -webkit-animation-iteration-count: 1 !important;
                animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}
@-webkit-keyframes background-pan {
    from {
        background-position: 0% center;
    }

    to {
        background-position: -200% center;
    }
}

@keyframes background-pan {
    from {
        background-position: 0% center;
    }

    to {
        background-position: -200% center;
    }
}

.animated-gradient {
    -webkit-animation: background-pan 3s linear infinite;
            animation: background-pan 3s linear infinite;
    background: linear-gradient(to right, var(--clr-primary), var(--clr-pink), var(--clr-yellow), var(--clr-primary));
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    white-space: nowrap;
    background-size: 200%;
    height: 1.5em;
}
